import { v4 as uuidv4 } from 'uuid';
import { ADD, DELETE, COMPLETE, UNCOMPLETE, EDIT } from './actions'

export const initialState = {
  toDos: [],
  completed: [],
}

// return ÌïòÎäî objectÍ∞Ä stateÏóê Îì§Ïñ¥Í∞ÄÍ≤å Îê† object (replace)
const reducer = (state, action) => {
  switch (action.type) {
    case ADD:
      return { ...state, toDos: [...state.toDos, { text: action.payload, id: uuidv4() }] };
    case DELETE:
      return { ...state, 
        toDos: state.toDos.filter(toDo => toDo.id !== action.payload),
        completed: state.completed.filter(complete => complete.id !== action.payload),
      };
    case COMPLETE:
      return { ...state, 
        completed: [...state.completed, state.toDos.find(toDo => toDo.id === action.payload)], 
        toDos: state.toDos.filter(toDo => toDo.id !== action.payload)
      };
    case UNCOMPLETE:
      return { ...state, 
        toDos: [...state.toDos, state.completed.find(complete => complete.id === action.payload)],
        completed: state.completed.filter(complete => complete.id !== action.payload)
      };
    case EDIT:
      const target = state.toDos.find((toDo) => toDo.id === action.id);
      const rest = state.toDos.filter((toDo) => toDo.id !== action.id);
      return {
        ...state,
        toDos: rest.concat({ ...target, text: action.payload })
      };
    default:
      throw new Error();
  }
};

export default reducer;